$templateColor: #fff;
$templateColor2: #db5330;
$templateColor3: #eb752a;
$templateColor4: #b84628;
$mediaQuery1000: 1000px;
$mediaQuery767: 767px;
$mediaQuery376: 376px;
$mediaQuery300: 300px;

.header,
.headMenu,
.mainTitle::before,
.thm .hit a {
  background-color: $templateColor3;
}

.mainTitle {
  h2 {
    width: 79.5%;
  }

  span {
    color: #cccccc;
  }
}

.lastEditDate {
  display: block;
  width: 100%;
  text-align: right;
  color: #cccccc;
}

.path a:hover,
.mti h3,
.thm li a:hover,
.out_link a {
  color: $templateColor3;
}

.top_menu > ul > li > a::after {
  color: $templateColor;
}

.subheader .tw::after {
  background-color: $templateColor;
}

.out_link a:hover {
  opacity: 0.6;
}

.right-main {
  border-left-color: $templateColor3;
}

.top_menu .submenu {
  background-color: $templateColor2;
}

.top_menu .submenu li:hover {
  background-color: $templateColor4;
}

.top_menu .icon {
  background-color: transparent;
}

.top_menu .submenu li:hover {
  background-color: $templateColor3;
}

.top_menu > ul > li {
  border-bottom-color: #f9a48e;
}

.copyhight {
  padding-top: 12px;
  padding-bottom: 12px;
}

.logo {
  top: 15px;
}

.subheader .tw {
  font-size: 10px;
  padding-top: 2.6px;
  &::after {
    left: 0;

    -webkit-transform: none;

    transform: none;
  }
}

footer {
  background: url(../img/footerBg.jpg) top left;
}

.thm .hit a {
  border-radius: 5px;
}

//tabs
.postcontent {
  .nav-tabs > li.active > a {
    color: $templateColor3;
    font-weight: 500;
  }
  .nav-tabs > li > a {
    color: black;
    text-decoration: none;
    font-weight: 500;
    transition-timing-function: ease-in-out;
    transition-duration: 0.3s;
    transition-property: border-color, background-color;

    &:hover {
      border-color: #eeeeee #eeeeee #dddddd;
      background-color: #eeeeee;
    }
  }
}

.bottomBanner {
  img {
    border-radius: 5px;
  }
}

.bottomBanner li {
  margin-top: 10px;
}

.qabox .qa_list {
  background-color: $templateColor3;
}

.qabox .qa_list.show {
  background-color: $templateColor2;
}

.top_menu > ul > li.closeBtn a::before {
  content: none;
}

.header.open .logo {
  top: 0;
}

.online.open {
  right: 0;
  transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out,
    -webkit-transform 0.4s ease-in-out, right 0.4s ease-in-out;
}

.open {
  .headMenu {
    height: 89px;
  }
}

.online a {
  text-decoration: none;
  margin-bottom: 5px;
  &:hover {
    right: 5px;
    opacity: 0.7;
    color: black;
  }
}

.top_menu > ul > li:nth-last-child(2) > a::after {
  content: none;
}

.leftNav {
  margin-top: 0px;
}

main {
  min-height: 825px;
}

.attactArea {
  display: block;
  position: relative;
  height: auto;
  overflow: auto;

  @media screen and (max-width: $mediaQuery1000) {
    padding-left: 2%;
  }

  h3 {
    font-size: 16px;
    font-weight: 700;
    margin-left: 8px;
    &::before {
      position: relative;
      top: 0px;
      left: -5px;
      display: inline-block;
      width: 2.5px;
      height: 20px;
      content: '';
      background-color: $templateColor3;
      vertical-align: top;
    }
  }

  a {
    display: block;
    float: left;
    clear: left;
    text-decoration: none;
    padding: 3px 0;
    &:hover {
      color: #bdbdbd;
      transition: color 0.3s ease-in-out;
    }
  }
}

.videoArea,
.slideArea {
  margin-top: 50px;
  display: block;
  width: 100%;
  clear: both;

  h3 {
    padding-left: 10px;

    display: block;
    width: 100%;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 10px;

    &::before {
      position: relative;
      top: 0px;
      left: -5px;
      display: inline-block;
      width: 5px;
      height: 22px;
      content: '';
      background-color: $templateColor3;
      vertical-align: top;
    }
  }
}

.videoArea {
  iframe {
    width: 100%;
    height: 100%;
    min-height: 500px;
    position: relative;
    // top: 100px;
  }
}

//同意書
.consentFormArea {
  background-color: rgba($templateColor3, 1);
  padding: 20px;
  .consentFormContent {
    height: 400px;
    overflow: auto;
    border: 1px solid #999;
    background-color: #fff;
    padding: 32px;
    p {
      margin-bottom: 0;
    }
  }
  .consentFormChk {
    margin: 10px 0;
    padding-left: 35%;
    color: #fff;

    @media screen and (max-width: $mediaQuery767) {
      padding-left: 0;
      width: 100%;
      text-align: center;
    }

    input[type='checkbox'] {
      margin-right: 15px;

      @media screen and (max-width: $mediaQuery767) {
        display: block;
      text-align: center;
      width: 100%;
        margin-right: 0;
      }
    }

    label{
      @media screen and (max-width: $mediaQuery767) {
        margin: 5px 0;
      }
    }
  }
  button {
    display: block;
    margin: 0 auto;
  }
}

.text-red{
  color: red;
}

.py-5{
  padding-top: 5px;
  padding-bottom: 5px;
}

.pt-15{
  padding-top: 15px;
}

.d-block{
  display: block;
}

@media only screen and (max-width: 1190px) {
  .logo {
    top: 22px;
  }
}

@media only screen and (max-width: 1000px) {
  .top_menu > ul {
    top: 91px;
  }

  .top_menu .submenu li a {
    padding: 10px 20px 10px 10px;
  }

  .top_menu > ul > li.closeBtn {
    margin-bottom: 20px;
  }

  .add,
  .sitmap {
    display: block;
    float: none;
    text-align: left;
    width: 100%;
    margin: 0 auto;
    width: 72%;
    padding-left: 0px;
  }

  .top_menu > ul > li.closeBtn a {
    background-color: $templateColor3;
    padding: 5px 10px;
  }

  .logo {
    top: 0px;
  }

  .top_menu .submenu li {
    background-color: #eee;
  }

  .top_menu > ul > li.closeBtn {
    background-color: #fff;
  }

  .top_menu > ul > li > a {
    background-color: #fff;

    &::before {
      transition: all 250ms;
    }
  }

  .top_menu > ul > li > a::before {
    top: -5px;
  }

  .top_menu > ul > li > a.on::before,
  .top_menu > ul > li > a::before,
  .top_menu > ul > li:not(.closeBtn) > a:hover::before {
    background: none;
    content: '\f105';
    font-family: FontAwesome;
  }

  .top_menuNoSub.top_menuNoSub a {
    padding-left: 74px;
    padding-bottom: 12px;
  }

  .top_menuNoSub.top_menuNoSub a.on::before,
  .top_menuNoSub.top_menuNoSub a::before,
  .top_menuNoSub.top_menuNoSub a:not(.closeBtn):hover::before {
    content: none;
  }

  .top_menu > ul > li > a {
    padding-bottom: 9px;
  }

  .mti {
    background: url(../img/touchMe.png) 100% 63% no-repeat;
    background-size: 40px;
  }
}
@media only screen and (max-width: $mediaQuery767) {
  .top_menu > ul > li > a {
    padding: 11px 15px 8px 15px;

    transition: padding 0.3s ease-in-out;

    &:hover {
      padding-bottom: 8px;
    }
  }

  .top_menu > ul > li.closeBtn a {
    background-color: $templateColor3;
    color: #efefef;
    padding: 5px 10px;
    width: 43%;
    transition: opacity 0.3s ease-in-out;

    &:hover {
      opacity: 0.7;
    }

    &::before {
      content: none;
    }
  }
}

@media only screen and (max-width: $mediaQuery376) {
  .logo {
    top: 6px;
  }
}
@media only screen and (max-width: $mediaQuery300) {
  .logo {
    top: 12px;
  }
}
